import React from 'react';
import styles from './SIngleDay.css'
import moment from 'moment';

const Single = ({el}) => {

    console.log(el);
    return (
  <li className={styles.card}>
                <p className={styles.mainTitle}>{moment.unix(el.date).format('dddd Do ')}</p>

        <img className={styles.img} src={`http://openweathermap.org/img/w/${el.icon}.png` }alt="icon"/>
           <p className={styles.degree}>{el.averageTemp}C&#176; </p>
           
           
           <p className={styles.title}>
           {console.log(el.wind)}<img src="https://img.icons8.com/ultraviolet/40/000000/hygrometer.png"/>
           Humidity        {el.humidity}%</p>
           <p className={styles.title}><img src="data:image/png;base64," alt='icom'/>Wind {Math.floor(el.wind.speed*1.60934)} KM/h</p>

        </li>

    );
};

export default Single;