import React from 'react';
import styles from './FavoriteList.css';
const FavoritList = ({elem,chan,delet,}) => {
    
    return (
        <li className={styles.li} id={elem} >
        
        <p className={styles.block} onClick={chan}>{elem}</p>
        <img id ={elem} onClick={delet} className={styles.span}src="data:image/png;base64,"/>
        </li>
    );
};

export default FavoritList;