import React from 'react';
import styles from './Menu.css';

import {NavLink} from 'react-router-dom';
import Today from '../Today/Today';
import FiveDays from '../FiveDays/FiveDays';
const Menu = () => {
  
    return (
      
        <ul className={styles.menu}> 
            <li className={styles.item} ><NavLink className={styles.correct} to='/App_Weather/'>Today</NavLink></li> 
            <li className={styles.item} ><NavLink className={styles.correct} to='/App_Weather/FiveDay'>Five Day</NavLink></li>
            </ul>
 
        
    );
};

export default Menu;