import React from 'react';
// import {Animated} from "react-animated-css";
import styles from './Button.css'
const Button = ({add,item,onAnim}) => {
    return (
        
        <div >
        <button  onClick={add} className={onAnim ? `${styles.img} ${styles.block} ${styles.animOn}` :`${styles.img} ${styles.block}`}> <span></span></button>
    </div>
    );
};

export default Button;
