import React from 'react';
import styles from './Info.css';
import Today from './Today/Today';
import Menu from './Menu/Menu';
import {Switch,Route} from 'react-router-dom'

import FiveDays from './FiveDays/FiveDays';
const Info = ({deg,wind,weather,sercFiveCity,input,hours,clickInfo,moreInfo}) => {
    return (
        <div className={styles.block}>
        <Menu/>

           {/* <Today deg={deg} wind ={wind}/> */}
           {/* <FiveDays weather={weather}  isLoading={isLoading}/> */}
           <Switch>
             <Route exact path='/App_Weather/' render={(props)=><Today {...props} deg={deg} wind ={wind} hours={hours} clickInfo={clickInfo} moreInfo={moreInfo}/>} />
             <Route path='/App_Weather/FiveDay'render={(props)=><FiveDays {...props} weather={weather}sercFiveCity={sercFiveCity} input={input}  />} />
       
         </Switch>
        </div>
         
            
    );
};

export default Info;