import React from 'react';
import {Bar,Line,Pie} from 'react-chartjs-2';
import styles from './Chart.css'
const Chart = ({char}) => {


    console.log(char);
    return (
        <div className={styles.main}>
            <Line
	data={char}

	options={{
        title:{display:true,
        text:'Chart Weather',
    fontSize:25,},
        legend:{
            display:true,
            position:'right',     },
            labels:{
                fontColor:'#fff'
            },
            
    }
		}
/>
        </div>
    );
};

export default Chart;