import React from 'react';
import styles from './Header.css';
import moment from 'moment';
const Header = ({time,change,values,country,deg}) => {
    return (
        <div className={styles.header}>
            <form action="" onSubmit={values}>
            <input type="text"  className={styles.inputText} onChange={change} />
            <input type="submit"className={styles.inputSubmit} value=""/>
            </form>
            <div className={styles.block}>
             
                <p className={styles.geo}>{country}</p>
            <p className={styles.geos}>{time}</p>

            </div>
            <p className={styles.mainTitle}>{country}</p>
            <img  className={styles.img} src="https://img.icons8.com/ultraviolet/80/3498db/place-marker.png" alt=""/>
            
           
            {/* <ul>
                <li className={styles.card}><p className={styles.degree}>{deg}&#176;</p>
                <p className={styles.title}>{moment().format("dddd Do ")}</p>
                <p className={styles.title}>{moment().format(" MMMM")}</p></li>
            </ul> */}
        </div>
    );
};

export default Header;