import React from 'react';
import styles from './Today.css'
import moment from 'moment';
const Today = ({deg,wind,hours,clickInfo,moreInfo}) => { 
    let res=`${wind.weather[0].icon}`;
  
  
    return (
        <div>
              <ul className={styles.weather}>
                <li className={styles.card}>
                <p className={styles.mainTitle}>{moment().format("dddd ")}</p>
                <p className={styles.title}>{moment().format(" MMMM D")}</p>
                <p className={styles.degree}>{deg}&#176;</p>
                <p className={styles.subtitle}><img src="https://img.icons8.com/ultraviolet/40/000000/thermometer.png"/>Temp MAX    {wind.main.temp_max} C&#176;</p>
                    <p className={styles.subtitle}><img src="data:image/png;base64,"/>Temp MIN  {wind.main.temp_min} C&#176;</p>
              </li>
                <li className={styles.twocard}>
                    <img className={styles.img} src={`http://openweathermap.org/img/w/${res}.png`}/>
                    
                    <p className={styles.subtitle}><img src="data:image/png;base64,"/>Wind {Math.floor(wind.wind.speed*1.60934)}KM/h  </p>    
                
                    <p className={styles.subtitle}><img src="https://img.icons8.com/ultraviolet/40/000000/hygrometer.png"/>
                    Humidity {wind.main.humidity}  %</p>
            
                </li>
                  
          
               
             {hours.map(el=><li key={el.dt} className={styles.otherCard}>
                <p className={styles.suptitle} ><img src={`http://openweathermap.org/img/w/${el.weather[0].icon}.png`} alt="icon"/></p>
                 <p className={styles.suptitle}>
temperature on   {el.dt_txt.split(" ")[1]}</p>
             <p className={styles.suptitle}>{el.main.temp.toFixed() } C&#176;</p>
            
            
             <p className={styles.suptitle}>{el.weather[0].description}</p></li>)}
  
            </ul>
            <p onClick={clickInfo} className={styles.moreInfo}>More Info </p>
            {moreInfo ?<ul className={styles.twoColumn}>
            {hours.map(el=><li key={el.dt} className={styles.mobiCard}>
               <p className={styles.suptitle} ><img src={`http://openweathermap.org/img/w/${el.weather[0].icon}.png`} alt="icon"/></p>
                <p className={styles.suptitle}>
 temperature on   {el.dt_txt.split(" ")[1]}</p>
            <p className={styles.suptitle}>{el.main.temp.toFixed() } C&#176;</p>
           
           
            <p className={styles.suptitle}>{el.weather[0].description}</p></li>)}
         
           </ul>:null}
        </div>
    );
};

export default Today;