import React, { Component } from 'react';
import Chart from "./Chart/Chart"
import styles from './FiveDays.css'
import moment from 'moment';
import Single from './SingleDay/Single';

class FiveDays extends Component {
    state={
        isLoader:true,
        chartData:[],
        textP:['Open Chart','Close Chart'],
        finalWorld:'Close Chart'

    }
    getChartData=()=>{
        // Ajax calls here
        let res=[]
        let resi=[]
        let hum=[]
         res=this.props.weather.map(el=>el.averageTemp)
         resi=this.props.weather.map(el=>moment.unix(el.date).format('dddd Do '))
        hum=this.props.weather.map(el=>el.humidity)
        console.log('What is this',resi);
        this.setState({
          chartData:{
            type:'line',
            labels: resi,
            
            datasets:[{
                label:'Humidity',
                data:hum,
                backgroundColor:[
                  'blue',
                ],
                borderColor: 'blue',
                fill:false,
                hidden:true,
                pointStyle: 'circle',
                pointRadius: 6,
                pointHoverRadius: 10,
              },
              {
                label:'Average temperature,℃',
                data:res,
                fill:false,
                backgroundColor:[
                 
                  'red',
                  
                ],
                borderColor: '#98ccfd',
                pointStyle: 'circle',
                pointRadius: 6,
                pointHoverRadius: 10,
              },
              
            ]
          }
        });
      }
      closeChart=()=>{
        this.setState(prev=>({
            isLoader:!prev.isLoader,
        }))
        if(!this.state.isLoader){
            this.setState({
                finalWorld:this.state.textP[0],
            })
        }else{
            this.setState({
                finalWorld:this.state.textP[1],
            })
        }
      }
    componentDidMount(){
        this.props.sercFiveCity(this.props.input)
        this.getChartData()
        console.log('What is this',);
        this.setState(prev=>({
            isLoader:false,
        }))
        
    }
    componentDidUpdate(prevProps ){
        if(prevProps.input!==this.props.input){
        this.props.sercFiveCity(this.props.input)
        this.getChartData()
        }
    }



    render() {
        console.log('General Array',this.props.weather);
        // console.log(this.props.weather);
        console.log('See here',this.props.input);
            return (
               <div>
                   <button className={styles.btn} onClick={this.closeChart}>{this.state.finalWorld}</button>
               <ul className={styles.weather}>
                   { this.props.weather.map((el)=><Single el={el} />)}
{console.log(this.state.chartData)}
               </ul>
               <p className={styles.charts}>
               {this.state.isLoader? <p></p> :<Chart  data={this.props.weather} char={this.state.chartData}/>
               }</p>
               
               </div>
            
            );
        };
    }

export default FiveDays;